@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  src: local("☺"), url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../fonts/OpenSans-Light.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: local("☺"), url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: local("☺"), url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: local("☺"), url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  color: var(--color-black);
  background-color: var(--color-white); }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.page--menu-open {
  height: calc(100vh + 200px);
  overflow: hidden; }

.page--modal-open {
  height: 100vh;
  overflow: hidden; }

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow-x: hidden; }

.page__footer {
  flex: 0 0 auto; }

.page__main {
  flex: 1 0 auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 20px; }
  @media (max-width: 1023.8px) {
    .container {
      max-width: 1023.8px; } }
  @media (max-width: 767.8px) {
    .container {
      padding: 0 14px; } }
